.retailerInfoCloseBtn  {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(50%, -50%);
    cursor: pointer;
    width: 24px;
    height: 24px;
    z-index: 1;
}
.close-x-circle {
    background: black;
    border-radius: 50%;
    cursor: pointer;
}

.retailer-info-spinner {
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.retailer-info-content {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.retailer-logo {
    text-align: center;
    margin-bottom: 30px;
    .img {
        max-width: 260px;
        max-height: 260px;
    }
}

.retailer-info-modal {
	max-width: 600px !important;
    min-width: 360px;
}

