.kioskMarker {
  position: relative;
  width: 25px;
  height: 40px;
  pointer-events: none;
}

.kioskMarker svg {
  transform: translate(-50%, -100%);
  position: absolute;
  top: 0;
}

.mapCenterMarker {
  pointer-events: none;
}

.mapCenterMarker:before,
.mapCenterMarker:after {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  left: 0;
  top: 0;
  margin-left: -5px;
  margin-top: -5px;
  border-radius: 50%;
  box-shadow: 0 0 0 0 rgb(76, 165, 255);
  transform: scale(1);
}

.mapCenterMarker:before {
  animation: pulse-animation 2s infinite;
  background-color: rgb(76, 165, 255);
}

.mapCenterMarker:after {
  animation: pulse-animation 2s infinite 0.4s;
}

@keyframes pulse-animation {
  0% {
    transform: scale(0.8);
    box-shadow: 0 0 0 0 rgba(76, 165, 255, 0.7);
  }
  70% {
    transform: scale(1.2);
    box-shadow: 0 0 0 30px rgba(76, 165, 255, 0);
  }
  100% {
    transform: scale(0.8);
    box-shadow: 0 0 0 0 rgba(76, 165, 255, 0);
  }
}
.mapbox-component {
  overflow: hidden;
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1;
  user-select: none;
}

.view {
  grid-template-columns: 1fr;
}

.mapbox-admin-form {
  background-color: rgba(0, 0, 0, 0.05);
  padding: 15px;
  border: 1px solid #ccc;
  min-width: 400px;
}

.mapbox-info {
  color: #31708f;
  background-color: #d9edf7;
  border-color: #bce8f1;
  padding: 15px;
  border: 1px solid transparent;
  border-radius: 4px;
  font-size: 14px;
  line-height: 1.42;
}

.hide {
  display: none !important;
}

