#threejs-map-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  flex: 1;
  user-select: none;
}

#threejs-map {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.map_controls {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1;
}
.map_controls :global .map_controls_wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.map_controls :global .map_controls_wrapper .control_btn {
  color: #FFF;
  background-color: #0763b1;
  font-size: 15px;
  font-weight: bold;
  padding: 6px 15px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 300ms;
  width: 100%;
  flex: 1;
  text-align: center;
}
.map_controls :global .map_controls_wrapper .control_btn:hover {
  background-color: #054983;
}
.map_controls :global .map_controls_wrapper .control_btn.active {
  background-color: #054983;
  cursor: default;
}
.map_controls :global .map_controls_wrapper .floors {
  align-items: normal;
}
.map_controls :global .map_controls_wrapper .floors .floor:not(:first-child):not(:last-child) {
  border-radius: 0;
}
.map_controls :global .map_controls_wrapper .floors .floor:first-child {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.map_controls :global .map_controls_wrapper .floors .floor:last-child {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.map_controls :global .map_controls_wrapper .reset-zoom {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.map_controls :global .map_controls_wrapper .reset-zoom .zoom_btns {
  display: flex;
}
.map_controls :global .map_controls_wrapper .reset-zoom .zoom_btns .control_btn {
  font-size: 17px;
  padding: 0;
  width: 36px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}
.map_controls :global .map_controls_wrapper .reset-zoom .zoom_btns .control_btn.zoom-out {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.map_controls :global .map_controls_wrapper .reset-zoom .zoom_btns .control_btn.zoom-in {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

#threejs-map .amenities {
  position: absolute;
  right: 0;
  top: 50%;
  border-bottom: none;
  text-align: center;
  transform: translateY(-50%);
  z-index: 1;
  cursor: default;
  display: none;
}

#threejs-map .amenities .amenity {
  cursor: pointer;
  display: flex;
  align-items: center;
  background-color: #000;
  transition: opacity 300ms;
  opacity: 0.7;
}

#threejs-map .amenities .amenity:hover {
  opacity: 1;
}

#threejs-map .amenities .amenity .img img {
  width: 3.2vw;
  height: 3.2vw;
  object-fit: contain;
  vertical-align: bottom;
  padding: 0.6vw;
}

#threejs-map .amenities .amenity .img svg {
  width: 3.2vw;
  height: 3.2vw;
  object-fit: contain;
  vertical-align: bottom;
  padding: 0.6vw;
  color: #FFF;
}

#threejs-map .amenities .amenity .name {
  font-size: 13px;
  padding-left: 6px;
  display: none;
  color: #fff;
}

#escalator_elems {
  display: none;
}

#escalator_elems .element {
  position: absolute;
  top: -18px;
  left: -18px;
  width: 36px;
  height: 36px;
  border: 4px solid #ccc;
  background-color: #4ea5ff;
  box-sizing: border-box;
  border-radius: 50%;
  background-image: url("https://mycenterportal.com/assets/threejs/assets/img/icons/escalator.svg");
  background-size: 80%;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  z-index: 1;
}

#escalator_elems .element:hover {
  z-index: 2;
}

#escalator_elems .element .label {
  display: none;
  background-color: #fff;
  white-space: nowrap;
  position: absolute;
  padding: 0 12px;
  height: 30px;
  border-radius: 15px;
  align-items: center;
  color: #444;
  font-family: Calibri;
  font-size: 18px;
  font-weight: bold;
  left: 50%;
  top: 0;
  transform: translate(-50%, calc(-100% - 12px));
  box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
}

/* #escalator_elems .element:hover .label { */
#escalator_elems .element .label {
  display: flex;
}

#escalator_elems .element .label:before {
  content: "";
  position: absolute;
  top: calc(100% - 8px);
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
  border: 6px solid #fff;
  border-color: transparent #fff #fff transparent;
}

.stats {
  position: absolute !important;
}